uditor list.')\n        }).finally(() => {\n//          this.form.comment = ''\n          this.loading = false\n        })\n      },\n      transformTime (time) {\n        let result = moment(time).format('DD/MM/YYYY, h:mm')\n        return result\n      }\n    },\n    computed: {\n//      commentIsEmpty () {\n//        return this.form.comment == null || this.form.comment.trim().length === 0\n//      }\n    },\n    mounted () {\n    }\n  }\n</script>\n<style scoped>\n  .gameActionButton {\n    margin-top: 5px;\n  }\n</style>\n"],"sourceRoot":""}]);

